.Pager {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    margin: 48px 0;
}

.Pager p {
    margin: 0 18px;
}
.Pager button {
    text-transform: uppercase;
    padding: 4px 18px;
    background-color: #3a99ac;
    border: 1px solid #3a99ac;
    -webkit-box-shadow: 0 2px 10px 0 rgb(46 108 153 / 20%);
            box-shadow: 0 2px 10px 0 rgb(46 108 153 / 20%);
    color: #fff;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border-radius: 4px;
}
.Pager button:hover {
    cursor: pointer;
    background-color: #3a99acb0;
}
.Pager button:focus {
    outline: none;
}
.Pager button:disabled {
    background-color: #c0d3e0;
    border-color: #c0d3e0;
    -webkit-box-shadow: none;
            box-shadow: none;
    cursor: not-allowed
}



