.List {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 32px;
}
@media screen and (max-width: 950px) {
    .List {
        width: 80%;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        list-style: none;
        margin: 0 10%;
    }
}



