.Home {
  text-align: center;
  width: 100%;
  height: 90vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 18px;
}

.HomeLogo {
  height: 40vmin;
  pointer-events: none;
  margin-bottom: 36px;
}

@media (prefers-reduced-motion: no-preference) {
  .HomeLogo {
    -webkit-animation: HomeLogo-spin infinite 10s linear;
            animation: HomeLogo-spin infinite 10s linear;
  }
}
@-webkit-keyframes HomeLogo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes HomeLogo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



